@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
body {
  margin: 0;
  font-family: 'Poiret One';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: whitesmoke !important;
}

.App {
  height: 100vh;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  }

.display-flexbox {
  display: flexbox;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.flex-all-center {
  align-items: center;
  align-self: center;
  align-content: center;
  justify-items: center;
  justify-self: center;
  justify-content: center;
}

.flex-end {
  display: flex;
  align-items: flex-end;
}

.flex-spread {
  justify-content: space-between;
}

.flex-even {
  justify-content: space-evenly !important;
}

.txt-st {
  font-family: 'Bebas Neue';
}

.txt-md {
  font-family: 'Orbitron';
  font-size: 1.7rem;
  margin: 0;
}

.txt-lg {
  font-size: 3rem;
  font-family: 'Orbitron';
}

.txt-xl {
  font-size: 4rem;
  font-family: 'Orbitron';
}

.txt-xxl {
  font-size: 5rem;
  font-family: 'Orbitron';
}

.txt-bold {
  font-weight: 700;
}

.font-0 {
  font-family: 'Poiret One';
}

 /* NAV BAR  */

 .navbar-custom {
    background-color: rgba(245, 245, 245, 0.6);
    width: 380px;
    height: 90vh;
    -webkit-backdrop-filter: blur(7px);
    backdrop-filter: blur(6px);
 }

 .selected {
     background-color: white;
     font-weight: 600;
 }

 .top-side-bar img {
     width: 200px;
     height: 200px;
     margin-top: 20px;
     object-fit: cover;
     object-position: top;
     border-radius: 50%;
 }

 .middle-side-bar ul {
     list-style: none;
     text-align: center;
     margin-top: 15px;
     text-align: left;
     margin-left: 100px;
 }

 .middle-side-bar li {
    font-family: 'Orbitron';
    font-size: 1.5rem;
    padding: 10px;
 }

 .middle-side-bar li:hover {
     width: 100%;
     background-color: white;
     z-index: 3;
     -webkit-backdrop-filter: blur(0);
             backdrop-filter: blur(0);
     cursor: pointer;
 }

 /* HOME PAGE  */

 .home-page {
     width: 75%;
     height: 90vh;
     z-index: 1;
     background-color: white;
 }

 .interactive-content {
     height: 100%;
     overflow: auto;
 }

 .home-basic {
     height: 100%;
 }

 /* CONTACT PAGE  */

 .find-me-title {
     text-decoration: underline;
 }

 .social-links a {
    color: rgb(34, 34, 34);
 }
 
 .social-links a:hover {
     color: gainsboro;
 }

 #free-site {
     color: black;
 }

 /* SKILLS PAGE  */

 .skills-page {
     overflow: auto;
     width: 100%;
     padding: 50px;
 }

 .skill-title {
     text-decoration: underline;
 }

 .skill-level-front, .skill-level-back, .skill-level-other {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 50px;
    justify-items: center;
    align-items: center;
 }

 .skill-type {
     max-width: 70% !important;
     display: flex;
     align-items: center;
 }

 .skill-type .txt-md, .lead {
     align-self: flex-start;
 }

 /* PORTFOLIO PAGE  */

 .portfolio-page {
     overflow: auto;
 }

 .portfolio-title{
     text-decoration: underline;
 }

 .personal-projects, .professional-projects {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
 }

 .pro-title {
     margin-top: 50px;
 }

 .project-item {
     width: 345px;
     justify-content: space-between;
     overflow: auto;
 }

 .item-main {
     height: 100%;
 }

 .item-head img {
     width: 100%;
 }

 .item-link {
    background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
    text-align: center;
    padding: 1rem;
 }

 .item-link a {
     color: white;
     font-family: 'Orbitron';
     padding: 15px 100px 15px 100px;
 }

 .item-link a:hover {
    font-weight: 600;
    color: white;
    text-decoration: none;
 }
.typewriter {
    overflow: hidden;
    border-right: .11em solid rgb(0, 0, 0);
    margin: 0 auto;
    /* letter-spacing: .05em; */
    -webkit-animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
            animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
  }
  
  /* The typing effect */
  @-webkit-keyframes typing {
    from { width: 0 }
    to { width: 70% }
  }
  @keyframes typing {
    from { width: 0 }
    to { width: 70% }
  }
  
  /* The typewriter cursor effect */
  @-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: rgb(0, 0, 0); }
  }
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: rgb(0, 0, 0); }
  }
*::-webkit-scrollbar {
    width: .75em;
    height: .5em;
  }
  
 *::-webkit-scrollbar-track {
  background-image: linear-gradient(to top, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
  }
  
 *::-webkit-scrollbar-thumb {
  background-color: gainsboro;
  outline: 5px solid black;
  /* border-radius: 15px; */
  }
