 /* NAV BAR  */

 .navbar-custom {
    background-color: rgba(245, 245, 245, 0.6);
    width: 380px;
    height: 90vh;
    -webkit-backdrop-filter: blur(7px);
    backdrop-filter: blur(6px);
 }

 .selected {
     background-color: white;
     font-weight: 600;
 }

 .top-side-bar img {
     width: 200px;
     height: 200px;
     margin-top: 20px;
     object-fit: cover;
     object-position: top;
     border-radius: 50%;
 }

 .middle-side-bar ul {
     list-style: none;
     text-align: center;
     margin-top: 15px;
     text-align: left;
     margin-left: 100px;
 }

 .middle-side-bar li {
    font-family: 'Orbitron';
    font-size: 1.5rem;
    padding: 10px;
 }

 .middle-side-bar li:hover {
     width: 100%;
     background-color: white;
     z-index: 3;
     backdrop-filter: blur(0);
     cursor: pointer;
 }

 /* HOME PAGE  */

 .home-page {
     width: 75%;
     height: 90vh;
     z-index: 1;
     background-color: white;
 }

 .interactive-content {
     height: 100%;
     overflow: auto;
 }

 .home-basic {
     height: 100%;
 }

 /* CONTACT PAGE  */

 .find-me-title {
     text-decoration: underline;
 }

 .social-links a {
    color: rgb(34, 34, 34);
 }
 
 .social-links a:hover {
     color: gainsboro;
 }

 #free-site {
     color: black;
 }

 /* SKILLS PAGE  */

 .skills-page {
     overflow: auto;
     width: 100%;
     padding: 50px;
 }

 .skill-title {
     text-decoration: underline;
 }

 .skill-level-front, .skill-level-back, .skill-level-other {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 50px;
    justify-items: center;
    align-items: center;
 }

 .skill-type {
     max-width: 70% !important;
     display: flex;
     align-items: center;
 }

 .skill-type .txt-md, .lead {
     align-self: flex-start;
 }

 /* PORTFOLIO PAGE  */

 .portfolio-page {
     overflow: auto;
 }

 .portfolio-title{
     text-decoration: underline;
 }

 .personal-projects, .professional-projects {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
 }

 .pro-title {
     margin-top: 50px;
 }

 .project-item {
     width: 345px;
     justify-content: space-between;
     overflow: auto;
 }

 .item-main {
     height: 100%;
 }

 .item-head img {
     width: 100%;
 }

 .item-link {
    background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
    text-align: center;
    padding: 1rem;
 }

 .item-link a {
     color: white;
     font-family: 'Orbitron';
     padding: 15px 100px 15px 100px;
 }

 .item-link a:hover {
    font-weight: 600;
    color: white;
    text-decoration: none;
 }